e,CACf,cAAe,CAClB,GAEG,aAAc,CADlB,WAGQ,aAAc,CACjB,oBAIG,WAAY,CACZ,aAAc,CACd,aAAc,CAtVtB,yBAtC4B,CAuC5B,oBAAqB,CAXrB,o8BAA0F,CAmWrF,cAID,sBADsB,CAEtB,qBAFsB,CAD1B,iCAMQ,WAAY,CACZ,WAAY,CACZ,aAAc,CA7WtB,o8BAA0F,CA+WrF,eAMD,aAHe,CAIf,mBAHsB,CAF1B,sBAQQ,aAPW,CAQX,mBAPkB,CAnX1B,mbAA0F,CA4XrF","sourcesContent":["$background-color: #efefef;\n$primary-color: #1b6b67;\n$contrast-color: #0d3634;\n$font-main-color: $primary-color;\n$shadow-color: rgba(0, 0, 0, 0.11);\n$main-font-faces: Bangers, Arial;\n\n$header-height: 10vh;\n\n@font-face {\n    font-family: \"Bangers\";\n    src: url(\"./assets/Bangers-Regular.ttf\") format(\"truetype\");\n}\n\n/// Stroke font-character\n/// @param  {Integer} $stroke - Stroke width\n/// @param  {Color}   $color  - Stroke color\n/// @return {List}            - text-shadow list\n@function stroke($stroke, $color) {\n    $shadow: ();\n    $from: $stroke * -1;\n    @for $i from $from through $stroke {\n        @for $j from $from through $stroke {\n            $shadow: append($shadow, $i * 1px $j * 1px 0 $color, comma);\n        }\n    }\n    @return $shadow;\n}\n\n/// @param  {Integer} $stroke - Stroke width\n/// @param  {Color}   $color  - Stroke color\n/// @return {Style}           - text-shadow\n@mixin stroke($color: white, $stroke: 1, $shadow-width: 0.4vh) {\n    text-shadow: stroke($stroke, $color), #{$shadow-width} #{$shadow-width} 0 #{$shadow-color};\n}\n\n//@mixin stroke($color: white, $size: 1px, $shadow-width: 0.4vh) {\n//  text-shadow: -#{$size} -#{$size} 0 $color, 0 -#{$size} 0 $color, #{$size} -#{$size} 0 $color, #{$size} 0 0 $color,\n//  #{$size} #{$size} 0 $color, 0 #{$size} 0 $color, -#{$size} #{$size} 0 $color, -#{$size} 0 0 $color,\n//  #{$shadow-width} #{$shadow-width} 0 #{$shadow-color};\n//}\n\n@mixin common-text() {\n    font-family: $main-font-faces;\n    letter-spacing: 0.2vh;\n}\n\nbody {\n    background-color: $background-color;\n    margin: 0;\n    padding: 0;\n    height: 100%;\n    overflow: hidden;\n}\n\n.app {\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n    padding-top: 12vh;\n}\n\nheader {\n    height: $header-height;\n    background-color: $primary-color;\n    position: fixed;\n    width: 100%;\n    z-index: 1;\n    box-shadow: 0 1vh $shadow-color;\n    display: flex;\n    align-content: center;\n    justify-content: center;\n    align-items: center;\n    border-bottom: 0.3vh solid $contrast-color;\n\n    & .header-text {\n        font-family: $main-font-faces;\n        color: white;\n        -webkit-text-stroke: 1 $contrast-color;\n        font-size: 6vh;\n        padding-top: 1vh;\n        @include common-text();\n        @include stroke($contrast-color, 3, 0.4vh);\n    }\n}\n\n.page-header {\n    height: 13vh;\n    width: 90%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-content: center;\n    align-items: center;\n}\n\n.main-content {\n    height: 58vh;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-content: center;\n    align-items: normal;\n}\n\nfooter {\n    height: 17vh;\n    // padding-bottom: 5vh;\n    display: flex;\n    width: 90%;\n    justify-content: center;\n    flex-direction: column;\n    align-items: center;\n}\n\nh1 {\n    $font-size: 7vh;\n    $letter-spacing: 0.5vh;\n    @include common-text();\n\n    padding: 1vh;\n    margin: 0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n\n    @include common-text();\n    background: -webkit-linear-gradient($primary-color, $contrast-color);\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n    color: $contrast-color;\n    font-size: $font-size;\n    letter-spacing: $letter-spacing;\n    height: fit-content;\n\n    &:before {\n        @include common-text();\n        position: absolute;\n        content: attr(title);\n        font-size: $font-size;\n        letter-spacing: $letter-spacing;\n        z-index: -1;\n        @include stroke(white, 2, 0.5vh);\n    }\n}\n\nh2 {\n    color: $contrast-color;\n    @include common-text();\n    @include stroke(white, 2, 0.3vh);\n    font-size: 4vh;\n    margin: 0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.invalid-text {\n    @include common-text();\n    font-size: 3vh;\n    color: $contrast-color;\n}\n\n.gradient-text {\n    $font-size: 10vh;\n    $letter-spacing: 0.9vh;\n\n    @include common-text();\n    background: -webkit-linear-gradient($primary-color, $contrast-color);\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n    color: $contrast-color;\n    font-size: $font-size;\n    letter-spacing: $letter-spacing;\n    height: fit-content;\n    padding: 1vh;\n\n    &:before {\n        @include common-text();\n        position: absolute;\n        content: attr(title);\n        font-size: $font-size;\n        letter-spacing: $letter-spacing;\n        z-index: -1;\n        @include stroke(white, 3, 0.7vh);\n    }\n}\n\ninput {\n    transition: border-bottom-color 0.2s;\n    border-width: 0 0 3px;\n    border-radius: 2vh;\n}\n\nfieldset {\n    border: 0;\n    padding: 0;\n    margin-bottom: 2vh;\n    display: flex;\n    flex-direction: column;\n}\n\nlabel {\n    display: inline-block;\n    font-size: 4vh;\n    text-align: left;\n    color: $contrast-color;\n    font-family: $main-font-faces;\n    letter-spacing: 0.1vh;\n    padding-left: 0.5vh;\n    //padding-bottom: 0.4vh;\n    @include common-text();\n    @include stroke(white, 1, 0.2vh);\n}\n\nselect {\n    font-family: $main-font-faces;\n    color: black;\n    border-width: 0.2vh;\n    border-color: rgba(0, 0, 0, 0.2);\n    font-size: 4vh;\n    min-height: 7vh;\n    min-width: 18vh;\n    padding: 1vh 6vh 1vh 3vh;\n    letter-spacing: 0.1vh;\n    box-shadow: 0.4vh 0.4vh $shadow-color;\n    border-radius: 1vh;\n\n    //custom chevron\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    background-image: url(\"./assets/chevron.svg\");\n    background-size: 3vh;\n    background-repeat: no-repeat;\n    background-position: 92% center;\n\n    &:active {\n        box-shadow: 0.6vh 0.6vh $shadow-color;\n    }\n}\n\nbutton {\n    background-color: $primary-color;\n    border: 0.6vh solid $contrast-color;\n    padding: 2vh 3vh;\n    border-radius: 1vh;\n    box-shadow: 0.5vh 0.5vh $shadow-color;\n    transition: background-color 0.05s;\n\n    & .button-text {\n        color: white;\n        font-size: 5vh;\n        padding: 0.3vh;\n        @include common-text();\n        @include stroke($contrast-color, 3, 0.4vh);\n    }\n\n    &:active {\n        background-color: #195f5c;\n        box-shadow: 0.8vh 0.8vh $shadow-color;\n    }\n}\n\n.box {\n    width: 80%;\n    display: flex;\n    flex-flow: row wrap;\n    place-content: center;\n    flex-direction: row;\n    flex-wrap: wrap;\n    align-content: center;\n    justify-content: center;\n}\n\n.color-box-container {\n    display: flex;\n    justify-content: center;\n    align-items: flex-end;\n    width: 80vw;\n    height: 45vh;\n    padding: 3vh 2vh;\n\n    .extra-info {\n        color: $contrast-color;\n        font-size: calc(3vh + 4vw);\n        display: flex;\n        justify-content: center;\n        flex-direction: column;\n        align-items: center;\n        @include common-text();\n        text-align: center;\n        @include stroke(white, 1, 0.2vh);\n    }\n\n    .color-box {\n        background-color: $primary-color;\n        padding: 1vh;\n        border-radius: 2vh 2vh 0 0;\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        @include stroke(white, 1);\n        width: 80%;\n        border: $contrast-color solid 0.7vh;\n        max-width: 8vw;\n        margin: 0.1rem;\n\n        .color-box-header {\n            font-size: calc(3vh + 3vw);\n            padding: 0 1vh;\n            color: white;\n            @include common-text();\n            margin-bottom: 0.5vh;\n